.sidebar {
    width: 250px;
    background-color: #333;
    color: white;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    padding: 20px;
  }
  
  .sidebar-menu {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar-menu li {
    margin: 20px 0;
    cursor: pointer;
    font-size: 18px;
  }
  
  .sidebar-menu li:hover {
    color: #ccc;
  }