/* Overall page container */
.your-files-page {
  margin-left: 250px; /* Assuming Sidebar is 250px wide */
  padding: 20px;
  background: #343a40;
  min-height: 100vh; /* Ensures full viewport height */
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Header styling */
.header {
  width: 100%;
  text-align: center;
  margin-bottom: 40px;
}

.files-header h1 {
  font-size: 36px;
  color: #ffffff;
  margin: 0;
}

/* Policies section */
.policies {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px; 
}

/* Square with plus symbol */
.square {
  width: 150px;
  height: 200px;
  background-color: #b2b7b2;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.square:hover {
  background-color: #5f5f5f;
  transform: scale(1.05);
}

.plus {
  font-size: 48px;
  color: white;
  font-weight: bold;
}

@media (max-width: 768px) {
  .your-files-page {
    margin-left: 0; /* No margin for smaller screens */
    padding: 10px;
  }

  .header h1 {
    font-size: 28px;
  }

  .square {
    width: 120px;
    height: 120px;
  }

  .plus {
    font-size: 36px;
  }
}